<template>
  <div class="mb-5">
    <h4 class="mb-1">P/L - USD</h4>

    <div class="rounded my-shadow table-60-vh"> 
      <b-table-simple no-border-collapse sticky-header bordered responsive small striped  class="rounded mb-0">
        <b-thead>
          <b-tr>
            <b-th style="min-width: 230px">Name</b-th>
            <b-th style="min-width: 230px">Group</b-th>
            <b-th style="max-width: 80px">Agent</b-th>
            <b-th>Equity</b-th>
            <b-th>Credit</b-th>
            <b-th>Withdrawable</b-th>
            <b-th>Daily Net Deposit</b-th>
            <b-th>Monthly Net Deposit</b-th>
            <b-th>Daily Volume</b-th>
            <b-th>Monthly Volume</b-th>
            <b-th>Daily P/L</b-th>
            <b-th>Monthly P/L</b-th>
            <b-th>Last Month P/L</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr class="text-right">
            <b-td class="text-center bg-black" colspan="3">Total</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.Equity, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.Credit, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.Withdrawable, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.DailyNetDeposit, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.MonthlyNetDeposit, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.DailyVolume, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.MonthlyVolume, 0)}}</b-td>
            <b-td :class="dataTotal.DailyPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.DailyPL, 0)}}</b-td>
            <b-td :class="dataTotal.MonthlyPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.MonthlyPL, 0)}}</b-td>
            <b-td :class="dataTotal.LastMonthPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.LastMonthPL, 0)}}</b-td>
          </b-tr>
          <b-tr v-for="(item, index) in companyPL" :key="index" class="text-right">
            <b-td class="text-left">{{item.properties.server}}-{{item.properties.login}}</b-td>
            <b-td class="text-left">{{item.properties.group}}</b-td>
            <b-td>{{formatMoney(item.value.Agent, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.Equity, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.Credit, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.Withdrawable, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.DailyNetDeposit, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.MonthlyNetDeposit, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.DailyVolume, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.MonthlyVolume, 0)}}</b-td>
            <b-td :class="item.value.DailyPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.DailyPL, 0)}}</b-td>
            <b-td :class="item.value.MonthlyPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.MonthlyPL, 0)}}</b-td>
            <b-td :class="item.value.LastMonthPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.LastMonthPL, 0)}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1">
        <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Company </b-button>
        <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Region </b-button>
        <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Team </b-button>
        <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Client </b-button>
        <b-button size="sm" class="my-shadow ml-1 button-active" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Account </b-button>
      </div>
    </div>
  </div>
</template>
<script>
import { BTableSimple, BThead, BTr, BTh, BTd, BTbody, BTfoot, BButton } from 'bootstrap-vue'
import { formatMoney } from '@core/utils/filter'
import Ripple from 'vue-ripple-directive'
import axios from '@axios'

export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      companyPL: [],
    }
  },

  methods: {
    formatMoney,
  },

  channels: {
    MetricDataUpdateChannel: {
      connected() {
         console.log('Connected to the chat channel');
      },
      rejected() {
         console.log('Rejected to the chat channel');
      },
      received(data) {
        console.log('Received ', data.payload.context.metric_definition.name)
        
        if(data.payload.context.metric_definition && data.payload.context.metric_definition.name){
          let metric_datum_id = data.payload.context.id
          let value = data.payload.context.value
          
          if(['CompanyPL', 'CompanyPL_Region'].includes(data.payload.context.metric_definition.name)){
            this.companyPL = this.companyPL.map(datum => {
              if(datum.id != metric_datum_id) return datum;
              return {...datum, value: value}
            })
          }
        }
        
      },
      disconnected() {
        console.log('Disconnected to the chat channel');
      }
    }
  },
  
  computed: {
    dataTotal(){
      let label = ['Equity', 'Credit', 'Withdrawable', 'DailyNetDeposit', 'MonthlyNetDeposit', 'DailyVolume', 'MonthlyVolume', 'DailyPL', 'MonthlyPL', 'LastMonthPL']
      let result = {}
      label.map( field => result[field] = 0)
      this.companyPL.map(item => {
        label.map( field => {
          if(item.value[field]) result[field] += item.value[field]
        })
      })
      return result
    }
  },
  
  mounted(){
    axios.get(`${process.env.VUE_APP_API_URL}metric/CompanyPL/data`).then(res => {
      this.companyPL = res.data 
    })

  }
}
</script>