<template>
  <div class="mb-3 d-flex justify-content-between">
    <div class="statics_card_width"> 
      <statistic-card-with-area-chart
        icon="SunsetIcon"
        :statistic="`$ ${formatMoney(pLThisMonth, 0)}`"
        statistic-title="P/L This month"
        :chart-data="[{
            data: pLInMonthAgo,
            name: 'Amount'
          }]"
        color="info"
      />
    </div>
      
    <div class="statics_card_width"> 
      <statistic-card-with-area-chart
        icon="DownloadIcon"
        :statistic="formatMoney(netDepositThisMonth, 0)"
        statistic-title="Net deposit this month"
        :chart-data="[{
            data: netDepositInMonthAgo,
            name: 'Amount'
          }]"
        color="success"
      />
    </div>
    
    <div class="statics_card_width"> 
      <statistic-card-with-area-chart
        icon="SunsetIcon"
        :statistic="formatMoney(pLToday, 0)"
        statistic-title="P/L Today"
        :chart-data="[{
            data: pLOnDayAgo,
            name: 'Amount'
          }]"
        color="danger"
      />
    </div>
    
    <div class="statics_card_width"> 
      <statistic-card-with-area-chart
        icon="DownloadIcon"
        :statistic="formatMoney(netDepositToday, 0)"
        statistic-title="Net Deposit Today"
        :chart-data="[{
            data: netDepositOnDayAgo,
            name: 'Amount'
          }]"
        color="warning"
      />
    </div>
      
    <div class="statics_card_width"> 
      <statistic-card-with-area-chart
        icon="SunsetIcon"
        :statistic="formatMoney(withdrawable, 0)"
        statistic-title="Withdrawable"
        :chart-data="[{
            data: withdrawableAgo,
            name: 'Amount'
          }]"
        color="secondary"
      />
    </div>
  </div>
</template>
<script>

import { formatMoney } from '@core/utils/filter'
import StatisticCardWithAreaChart from '@core/components/statistics-cards/StatisticCardWithAreaChart.vue'
import axios from '@axios'


export default {
  components: {
    StatisticCardWithAreaChart,
  },
  data() {
    return {
      pLThisMonth: 0,
      netDepositThisMonth: 0,
      pLToday: 0,
      netDepositToday: 0,
      withdrawable: 0,

      pLInMonthAgo: [11123, 341241, 531414, 45424, 673242, 674234, 454234],
      netDepositInMonthAgo: [11, 34, 53, 45, 67, 67, 45],
      pLOnDayAgo: [11, 34, 53, 45, 67, 67, 45],
      netDepositOnDayAgo: [11, 34, 53, 45, 67, 67, 45],
      withdrawableAgo: [11, 34, 53, 45, 67, 67, 45],

    }
  },
  methods: {
    formatMoney,
  },

  mounted() {
    axios.get(`${process.env.VUE_APP_API_URL}metric/CurrentUserStatistics/data`).then(res => {
      res.data.map(item => {
        if(item.value.PLThisMonth) this.pLThisMonth += item.value.PLThisMonth
        if(item.value.NetDepositThisMonth) this.netDepositThisMonth += item.value.NetDepositThisMonth
        if(item.value.PLToday) this.pLToday += item.value.PLToday
        if(item.value.NetDepositToday) this.netDepositToday += item.value.NetDepositToday
        if(item.value.Withdrawable) this.withdrawable += parseFloat(item.value.Withdrawable)
      })
    })
  }
}
</script>
<style scoped>
  .statics_card_width {
    width: 18%;
    min-width: 327px;
  }
</style>